body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.grid-container {
  margin: 20px auto;
  background-color: #d8d8d8;
  border-radius: 10px;
  padding: 20px;
  border: 5px solid grey;
}

.small-ui-margin {
  margin: 5px;
}

.content-center-flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content-center-row-flex {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.floor-tile {
  background: radial-gradient(circle at 30% 30%, rgba(255,255,255,0.7), rgba(255,255,255,0) 40%),
              linear-gradient(135deg, #cdd7e1, #b8c5d6);
  box-shadow: inset 0 1px 0 rgba(255,255,255,0.6);
  border-radius: 10px;
}
.wall-tile {
  background: linear-gradient(180deg, #6b7280, #4b5563);
  box-shadow: inset 0 4px 0 rgba(255,255,255,0.3), inset 0 -6px 12px rgba(0,0,0,0.5);
  border-radius: 12px;
}
.goal-marker {
  position: relative;
  top: -8%;
  text-align: center;
}
.box-sprite {
  position: relative;
  top: -10%;
  text-align: center;
}
.player-sprite {
  position: relative;
  top: -10%;
  text-align: center;
}
.hud-btn {
  margin: 5px;
}
.pad-button {
  margin: 2px;
  font-size: 36px;
}
.grid-row {
  display: flex;
}
.pad-button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}